/*FONTS*/

div.reactive-auth-login h1, div.reactive-auth-login h2, div.reactive-auth-login h3, div.reactive-auth-login h4, div.reactive-auth-login h5, div.reactive-auth-login h6 { font-family: 'Ubuntu'; }

div.reactive-auth-login a { color: #468894; }
div.reactive-auth-login a:hover { color: #366871; }

div.reactive-auth-login p, div.reactive-auth-login input { font-family: 'Lato'; }

div.reactive-auth-login h1 {
    color: #425F6F;
    font-size: 24px;
    font-weight: bold;
}

/*END FONTS*/

/*FORMS*/
div.reactive-auth-login form table th {
    text-align: left;
}

div.reactive-auth-login select {
    width: 100%;
    height: 46px;
    margin-bottom: 10px;
    padding: 0px 15px;
    color: #858584;
    border: 1px solid #C3C3C3;
    font-size: 16px;
    box-sizing: border-box;
}

div.reactive-auth-login input {
    width: 100%;
    font-size: 16px;
    box-sizing: border-box;
}

div.reactive-auth-login input[type='checkbox'] {
    width: inherit;
}
div.reactive-auth-login input[type="text"],
div.reactive-auth-login input[type="password"] {
    height: 46px;
    padding: 0px 15px;
    margin-bottom: 10px;
    color: #858584;
    border: 1px solid #C3C3C3;
}

div.reactive-auth-login input[type="text"]:focus,
div.reactive-auth-login input[type="password"]:focus {
    color: #545554;
    border: 1px solid #425F6F;
    outline: 0;
}

div.reactive-auth-login input[type="text"]:hover,
div.reactive-auth-login input[type="password"]:hover {
    color: #858584;
    border: 1px solid #425F6F;
    box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.35);
    font-weight: normal;
}

div.reactive-auth-login input[type="submit"] {
    height: 46px;
    margin-bottom: 10px;
    width: 100%;
    background-color: #468894;
    color: white;
    text-transform: Uppercase;
    border: 0px;
    font-size: 18px;
    letter-spacing: 1px;
    font-weight: bold;
    border: 1px solid #468894;
}

div.reactive-auth-login input[type="submit"]:hover {
    background-color: #3b6f77;
    cursor: pointer;
}
/*END FORMS*/

/*CUSTOM LOGIN BUTTONS*/
div.reactive-auth-login ul#loginlist {
    padding: 0;
    overflow: hidden;
}

div.reactive-auth-login ul#loginlist li {
    background-position: top left;
    background-repeat: no-repeat;
    text-align: center;
    width: 48%;
    height: 46px;
    float: left;
    margin: 1%;
    display: block;
    position: relative;
    overflow: hidden;
}

div.reactive-auth-login ul#loginlist li:hover {
    position: relative;
    top: -5px;
    left: -3px;
}

div.reactive-auth-login #loginlist li a > span:first-child {
    position: absolute;
    left: 3px;
    top: 3px;
    font-size: 40px;
}

div.reactive-auth-login ul#loginlist li a {
    color: white;
    text-decoration: none;
    line-height: 46px;
    display: block;
}

div.reactive-auth-login ul#loginlist li:hover {
    -webkit-box-shadow: 5px 3px 0 1px rgba(0, 0, 0, 0.35);
    -moz-box-shadow: 5px 3px 0 1px rgba(0, 0, 0, 0.35);
    box-shadow: 5px 3px 0 1px rgba(0, 0, 0, 0.35);
}

div.reactive-auth-login .btn-fb {
    background-color: #3C5B96;

}
div.reactive-auth-login .btn-tw {
    background-color: #33A8DD;
}
div.reactive-auth-login .btn-go {
    background-color: #CA3937;
}
div.reactive-auth-login .btn-wl {
    background-color: #1962ad;
}
div.reactive-auth-login .btn-sko {
    background-color: #344dcc;
}
/*END CUSTOM LOGIN BUTTONS*/

/*CUSTOM STYLES*/
div.reactive-auth-login .box-container.section {
    max-width: 638px;
    margin-top: 50px;
}

div.reactive-auth-login .box-footer {
    margin-top: 10px;
}

div.reactive-auth-login .center-text {
    text-align: center;
}

div.reactive-auth-login .box-footer a {
    color: white;
}

div.reactive-auth-login .border-title {
    border-bottom: 1px solid #C3C3C3;
    line-height: 0.1em;
    margin: 25px 0 25px 0;
    text-align: center;
}

div.reactive-auth-login .border-title span {
    background-color: white;
    padding: 0 10px;
}

div.reactive-auth-login .float-left {
    float: left;
}

div.reactive-auth-login .float-right {
    float: right;
}
div.reactive-auth-login .formlinks {
    overflow: hidden;
    clear: both;
}

div.reactive-auth-login p.formlinks a {
    color: #545554;
}

div.reactive-auth-login a.underline {
    text-decoration: underline !important;
}

div.reactive-auth-login p.terms {
    color: #858584;
    font-size: 12px;
    clear: both;
}
/*
 * Cancel bootstrap css
 */
div.reactive-auth-login label.terms {
    display: inline; /* override bootstrap */
    margin-bottom: 10px; /* override bootstrap */
    font-weight: normal; /* overrride bootstrap */
    color: #858584;
    font-size: 12px;
    clear: both;
}

div.reactive-auth-login div.terms div.terms-field {
    margin-bottom: 10px;
    text-align: center;
}

div.reactive-auth-login ul.anchorlist {
    list-style-type: none;
    padding:0
}

div.reactive-auth-login .ptitle {
    color: #468894;
    font-size: 19px;
}

div.reactive-auth-login .eyeholder {
    background-image: url("../images/BB_icn_05_eye1.svg");
    height: 46px;
    width: 11.66%;
    background-size: 35%;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
}

div.reactive-auth-login div.box {
    box-shadow: none !important;
    background-color: white;
    overflow: hidden;
    padding: 20px 50px;
}

/*END CUSTOM STYLES*/

/*MEDIA QUERIES*/
@media all and (max-width:767px) {
    div.reactive-auth-login ul#loginlist li {
        width: 100%;
        margin: 1% 0;
    }
    div.reactive-auth-login .box-container.section {
        margin-top: 0;
    }
    div.reactive-auth-login div.box {
        padding: 20px 30px;
    }

    div.reactive-auth-login .eyeholder {
        width: 30px;
        background-size: 100%;
        margin: 0 auto;
    }
}

@media all and (min-width: 768px) {
    div.reactive-auth-login .surikatt {
        background-image: url("../images/BB_img_authun.svg");
        background-repeat: no-repeat;
        background-position-x: 45px;
        background-position-y: 100%;
        background-size: 140px;
    }

    div.reactive-auth-login input#repeatpassword.col-sm-5 {
        margin-left: 5%;
    }
}
/*END MEDIA QUERIES*/

div.reactive-auth-login p.login-error {
    border-radius: 3px;
    border: 1px solid red;
    background-color: #ffcccc;
    padding: 5px;
    margin-top: 13px;
    margin-bottom: 13px;
}

div.reactive-auth-login .terms-and-conditions-text-and-title {
    top: 0px;
    bottom: 40px;
    left: 0px;
    overflow: auto;
    position: absolute;
    padding-left: 40px;
}
div.reactive-auth-login div.terms-and-conditions-buttons {
    position: absolute;
    height: 35px;
    bottom: 0px;
    left: 50%;
    transform: translate(-50%, 0%);
}

div.reactive-auth-login .terms-and-conditions-buttons {
    margin-left: 7px;
    margin-right: 7px;
}

div.reactive-auth-login .embedded-signup-heading p {
    font-size: 14px;
    color: black;
}
