
div.reactive-auth-login {
    padding: 20px 50px;
}

div.reactive-auth-login #loginlist li {
    list-style-type: none;
}
div.reactive-auth-login #loginlist a img {
    border: 1px solid black;
}
div.reactive-auth-login #loginlist li.btn-feide {
    background-color: #be1527;
}
div.reactive-auth-login #loginlist li.btn-feide a {
    color: white;
}
div.reactive-auth-login #loginlist li.btn-feide a img {
    position: absolute;
    top: 3px;
    left: 3px;
    width: 40px;
    height: 40px;
    border: 0px;
}

div.reactive-auth-login .terms-and-conditions-title {
    padding: 15px;
}
div.reactive-auth-login .terms-and-conditions-text {
    max-width: 100%;
    max-height: 300px;
    overflow-x: hidden;
    overflow-y: auto;
}

div.reactive-auth-login div.enterprise-logins {
    position: relative;
    width: 100%;
}

div.reactive-auth-login div.enterprise-logins > a {
    position: absolute;
    right: -50px;
    border: 1px solid #fb5;
    color: #0ad;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 0px 0px 0px 5px;
    -webkit-border-radius: 0px 0px 0px 5px;
    -moz-border-radius: 0px 0px 0px 5px;
    top: -40px;
}
div.reactive-auth-login div.enterprise-logins > a:hover {
    background-color: #eee;
}